<template>
  <div class="membership-view-page">
    <!--  1      -->
    <LogoHeader />

    <!--  ,  ,   -->
    <div class="menu">
      <div class="menu-item" :class="{ 'active-menu-item': activeMenu === '/mypage/reservations' }" @click="navigateTo('/mypage/reservations')"> </div>
      <div class="menu-item" :class="{ 'active-menu-item': activeMenu === '/mypage/membership' }" @click="navigateTo('/mypage/membership')"> </div>
      <div class="menu-item" :class="{ 'active-menu-item': activeMenu === '/mypage/edit-profile' }" @click="navigateTo('/mypage/edit-profile')">  </div>
    </div>

    <!--     -->
    <div class="membership-view-content" v-if="grade">
      <h2>    </h2>

      <!-- /   -->
      <div class="membership-info" v-if="grade !== 'bronze'">
        <div class="membership-detail">
          <p>
            <strong>   : </strong> {{ year }}  {{ grade.toUpperCase()}} 
            <span class="membership-image-container">
              <span v-if="grade === 'gold'">
                <img :src="goldImage" alt=" " class="membershipLevel-image" />
              </span>
              <span v-else-if="grade === 'silver'">
                <img :src="silverImage" alt=" " class="membershipLevel-image" />
              </span>
            </span>
          </p>
          <p><strong>  : {{ user.name }}</strong> </p>
          <p><strong>  : </strong> {{ membershipDetails.membershipPrice }}</p>
          <p><strong>  : </strong>
            <span v-if="membershipDetails.membershipSalesStatus === 'PURCHASE'"> </span>
            <span v-else-if="membershipDetails.membershipSalesStatus === 'CONFIRMED'"> </span>
            <span v-else-if="membershipDetails.membershipSalesStatus === 'REFUNDED'"> </span>
          </p>
          <p><strong>  : </strong> {{ formattedPaymentDate }}</p>
        </div>
        <div class="cancel-button-container">
          <button 
            @click="MembershipCancel" 
            class="cancel-membership-button" 
            :disabled="membershipDetails.membershipSalesStatus === 'CONFIRMED' || membershipDetails.membershipSalesStatus === 'REFUNDED'"
            :class="{ 'disabled-button': membershipDetails.membershipSalesStatus === 'CONFIRMED' || membershipDetails.membershipSalesStatus === 'REFUNDED' }">
             
          </button>
        </div>
        <p class="membership-note">
                ,  7     .
        </p>
      </div>

      <!--    -->
      <div class="membership-info-bronze" v-else-if="grade === 'bronze'">
        <p><strong>  .</strong></p>
        <p><strong>  .</strong></p>
        <button @click="goToMembershipInfo" class="btn-goToMembershipInfo"> </button>&nbsp;&nbsp;<button @click="goToMembershipPurchase" class="btn-goToMembershipPurchase"> </button>
      </div>
    </div>
  </div>
</template>

<script>
import LogoHeader from '../common/LogoHeader.vue';

export default {
  name: 'MembershipView',
  components: {
    LogoHeader,
  },
  props: {
    user: {
      type: Object,
      default: () => ({ name: '', role: 'guest', email: '', isLoggedIn: false })
    }
  },
  data() {
    return {
      activeMenu: this.$route.path,
      membershipDetails: null,
      goldImage: require('@/assets/img/membershipImg/gold.png'),
      silverImage: require('@/assets/img/membershipImg/silver.png'),
      bronzeImage: require('@/assets/img/membershipImg/bronze.png'),
      grade : '',
      year : '',
    };
  },
  watch: {
    $route(to) {
      this.activeMenu = to.path;
    }
  },
  computed: {
    formattedPaymentDate() {
      if (!this.membershipDetails || !this.membershipDetails.membershipSalesCreateAt) {
        return '';
      }
      return this.membershipDetails.membershipSalesCreateAt.split('T').join(' ');
    }
  },
  mounted() {
    this.fetchEvents();
  },
  methods: {
    navigateTo(route) {
      this.$router.push(route);
      this.activeMenu = route;
    },
    goToMembershipInfo() {
      this.$router.push('/membership/info');
    },
    goToMembershipPurchase() {
      this.$router.push('/membership/purchase');
    },
    async fetchEvents() {
      console.log('---------------' + this.user.email);

      const params = new URLSearchParams();
      params.append('email', this.user.email);

      try {
        const response = await this.$axios.post("/myPage/membership", null, { params: params });
        
        this.membershipDetails = {
          membershipName: response.data.membershipName || ' ',
          membershipPrice: response.data.membershipPrice || ' ',
          membershipSalesStatus: response.data.membershipSalesStatus || '  ',
          membershipSalesCreateAt: response.data.membershipSalesCreateAt || ' '
        };
        
        const [year, grade] = this.membershipDetails.membershipName.split('-');
        this.grade = grade;
        this.year = year;
      } catch (error) {
        console.error('   :', error);
      }
    },
    MembershipCancel(){
    const params = new URLSearchParams();
    params.append('userEmail', this.user.email);
    console.log('' + this.user.email);

    this.$axios.post("/membership/cancel", null, { params: params })
    .then((response)=>{
      console.log(response);
      alert('  .');
      window.location.reload();
      
    })
  }
}
}
</script>

<style scoped>
.membership-view-page {
  padding-top: var(--header-height);
  padding-bottom: var(--footer-height);
  text-align: center;
}

/*  */
.menu {
  display: flex;
  justify-content: center;
  gap: 50px;
  padding-bottom: 20px;
}

.menu-item {
  width: 120px;
  padding: 10px 0;
  position: relative;
  cursor: pointer;
  font-weight: bold;
  color: #565656;
}

.menu-item::after {
  content: "";
  position: absolute;
  bottom: -5px; /*       */
  left: 0;
  width: 100%;  /*   div   */
  height: 3px;
  background-color: black;
  transform: scaleX(0); /*    */
  transition: transform 0.3s ease;
}

.active-menu-item {
  color: black;  /*     black  */
}

.active-menu-item::after {
  transform: scaleX(1);  /*     */
}

.menu-item:hover::after {
  transform: scaleX(1);  /*     */
}

/*   */
.membership-info {
  background-color: #f9f9f9;
  padding: 20px;
  margin: 20px auto;
  width: 80%;
  max-width: 600px;
  border-radius: 10px;
  border: 2px solid #60a191;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  text-align: left;
  margin-bottom: 50px;
}

.membership-info-bronze {
  background-color: #f9f9f9;
  padding: 20px;
  margin: 20px auto;
  width: 80%;
  max-width: 600px;
  border-radius: 10px;
  border: 2px solid #60a191;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  text-align: center;
  margin-bottom: 50px;
}

.membership-detail p {
  margin: 10px 0;
  font-size: 18px;
}

.cancel-button-container {
  display: flex;
  justify-content: flex-end;
  margin-top: 0px;
  margin-bottom: 20px;
}

.cancel-membership-button {
  background-color: #d9534f;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.cancel-membership-button:hover {
  background-color: #c9302c;
}

.membership-note {
  font-size: 14px;
  color: #666;
  margin-top: 10px;
}

.membershipLevel-image{
  width: 40px;
}

button{
  background-color: #4f8578;
  color: white;
  border: none;
  padding: 10px;
  width: 150px;
  height: 53px;
  margin-top: 10px;
  margin-left: 10px;
  border-radius: 5px;
  cursor: pointer;
}

.membership-detail p {
  font-size: 20px;
}

.disabled-button {
  background-color: gray;
  cursor: not-allowed;
}
</style>
